// this script is to display or hide certain elements on a page according to request
import jss from "jss";
import preset from "jss-preset-default";

jss.setup(preset());

// Create your style.
const style = {
  myButton: {
    color: "green",
  },
  "@global": {
    "#bcpo": {
      marginLeft: "15px",
    },
    "select.single-option-selector": {
      borderColor: "black",
    },
  },
};

// Compile styles, apply plugins.
const sheet = jss.createStyleSheet(style);

// If you want to render on the client, insert it into DOM.
sheet.attach();

